<template>
  <div :class="['collapsible-card', ...collapsibleClasses]">
    <div @click="toggleCollapse" class="collapsible-card__header">
      <div>
        <slot name="title"></slot>
      </div>
      <i v-if="!isDisabled" class="material-icons collapsible-arrow">expand_more</i>
    </div>
    <div class="collapsible-card__content">
      <slot :open="isCollapse"></slot>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, computed, watch } from 'vue'

export default defineComponent({
  name: 'CollapsibleCard',
  props: {
    isCollapse: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: String,
      default: '900px'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    bgcolor: {
      type: String,
      default: 'white'
    }
  },
  setup(props, { emit }) {
    const isCollapse = ref(props.isCollapse)

    const toggleCollapse = () => {
      if (props.disabled) return
      isCollapse.value = !isCollapse.value
      if (isCollapse.value) emit('open', isCollapse.value)
    }
    const collapsibleClasses = computed(() => {
      const classes = []
      if (isCollapse.value) classes.push('collapsible-card--collapse')
      return classes
    })
    const maxHeight = computed(() => {
      return props.maxHeight === 'none' ? '9999px' : props.maxHeight
    })
    const isDisabled = computed(() => props.disabled)

    watch(() => props.isCollapse, (isCollapsed) => isCollapse.value = isCollapsed)

    return {
      isCollapse,
      isDisabled,
      maxHeight,
      collapsibleClasses,
      toggleCollapse
    }
  }
})
</script>
<style lang="scss" scoped>
.collapsible-card {
  box-shadow: 0px 14px 12px -10px #0000001a;
  .collapsible-card__header,
  .collapsible-card__content {
    @apply p-6 bg-white overflow-y-hidden;
  }
  .collapsible-card__content {
    @apply transition-all duration-300 border-t-0 border-grey-lighten overflow-y-auto rounded-b-xl;
    padding-top: 0px;
    padding-bottom: 0px;
    max-height: 0px;
  }
  .collapsible-card__header {
    @apply flex justify-between items-center box-content cursor-pointer border-grey-01 select-none rounded-t-xl rounded-b-xl;
  }
  .collapsible-arrow {
    @apply transition-transform duration-300 transform rotate-0;
  }
  &.collapsible-card--collapse {
    .collapsible-card__header {
      @apply rounded-b-none;
    }
    .collapsible-card__content {
      @apply p-6 border-t;
      max-height: v-bind(maxHeight);
    }
    .collapsible-arrow {
      @apply rotate-180;
    }
  }
}
</style>
